@import url(https://fonts.googleapis.com/css?family=Merriweather:100);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
body {
  margin: 0;
  background: #ffd200;
  font-family: -apple-system, BlinkMacSystemFont, 'Open Sans', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body,
html {
  height: 100%;
}

.App,
.wrapper {
  height: 100%;
}

* {
  box-sizing: border-box;
  padding: 0;
  transition: all .2s;
}

a {
  text-decoration: none;
}

::-webkit-scrollbar-track {
  background-color: #f5f5f5;
}

::-webkit-scrollbar {
  width: 10px;
  background-color: #f5f5f5;
}

::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, .07);
}

/* Make the selected text red on a yellow background
   Code for Firefox */

::selection {
  color: #fff;
  background: rgb(143, 221, 202);
}
input[type="text"]{background-color:transparent;border:0;border-bottom:solid 1px #f16051;width:92%;padding-bottom:4px;color:#fff !important;font-weight:lighter;letter-spacing:2px;margin-bottom:30px;margin-right:20px;font-size:20px}input[type="text"]{outline:none}input:-webkit-autofill{-webkit-box-shadow:0 0 0 30px #2c3e50 inset;-webkit-text-fill-color:#fff !important}button{border:0;padding:8px 20px;margin:0 2px;border-radius:2px;font-weight:lighter;letter-spacing:1px;font-size:15px;cursor:pointer;background-color:#f16051;color:#fff;font-weight:100}button:active{outline:none}

